import React from 'react';
import styled from "styled-components";
import logo from "../assets/ic_launcher.png";
import Utils from './Utils';
import ToastMessage from "./ToastMessage";

class Home extends React.Component {

  timerInterval = 0;
  userModel = null;
  userState = null;
  workerProcess = null;

  constructor(props) {
    super(props);
    this.state = {
      userState : this.props.Model.state,
      toastMessage: '',
    }
    this.userModel = this.props.Model;
    Utils.logText('Home - ' + Object.getOwnPropertyNames(this.props.Model));
    this.userState = this.props.Model.state;
    Utils.logText('User state - ' + this.userState);
  }

  async onWorkerMessage(ev) {
    this.setBatteryState({workerProcess: { instance: this.workerProcess, started: false }})
    this.callNotification();
    Utils.logText(Utils.getBatteryTimeText(Date.now()) + ' : ' + ev.data);
  }

  setBatteryState = async (batteryOptions) => {
    this.setState({
      userState: this.userModel.setUserState(
        this.userState = {
          ...this.userState,
          ...batteryOptions,
        }
      )
    },);
  }

  getBatteryAPI = async () => {
      //Utils.logText(this.userState);
      await navigator.getBattery().then(battery => this.setBatteryState({ batteryLevel: battery.level, batteryState: battery.charging, currBatteryTime: Date.now(), }));
  }

  registerNotificationwithAccess = async () => {
    if (window.Notification && Notification.permission !== "granted") {
      this.setState({ toastMessage: [<span>Kindly grant notification access for this app to work</span>] }
        , () => Utils.logText('toastmessage: ',this.state.toastMessage));
      Notification.requestPermission(function (status) {
        if (Notification.permission !== status) {
          Notification.permission = status;
        }
      }).then(status => {
        Utils.logText('Notification Status : ',status)
        if(status === 'granted') {
          this.setState({ toastMessage: '' }
          , () => Utils.logText('toastmessage: ',this.state.toastMessage)); 
        }             
      });
    }
  }

  callNotification = async () => {
    Utils.logText('Notification called @ '+ Utils.getBatteryTimeText(this.userState.currBatteryTime));
    const notificationBody = Utils.getBatteryStateText(this.userState.notifyBatteryState) + ' with ' + this.userState.notifyBatteryLevel.toString() + '% is now complete.';
    await Utils.showNotification(this.props.AppTitle, logo, notificationBody);
    Utils.logText('Notification posted @ '+ Utils.getBatteryTimeText(this.userState.currBatteryTime));
  }

  getChangesLocally = async () => {
    Utils.logText('checking for local values ..');
    const localValues = await Utils.getItemsLocally(Utils.getDbContext());
    Utils.logText('local values count : ',[...localValues].length);   
    [...localValues].forEach(s => { 
      this.setBatteryState(s) ;
      Utils.logText('local value is : ',s);
    })
  }

  async componentDidMount() {
    if ('getBattery' in navigator) {
      Utils.logText(Utils.getBatteryTimeText(this.userState.currBatteryTime) + ' - The Battery Status API is supported on this platform.');
      this.getChangesLocally();
      this.registerNotificationwithAccess();
    } 
    else {
      this.setState({ toastMessage: [<span>Battery API is not supported in current browser, please try the link in <a target="blank" href="https://www.google.com/chrome/">Chrome</a> browser</span>] });
      Utils.logText('toastmessage: ',this.state.toastMessage);
    }

    this.timerInterval = setInterval(async () => { 
      await this.getBatteryAPI() 
      if(this.userState.notificationSent === false && this.userState.batteryState === this.userState.notifyBatteryState && this.userState.batteryLevel*100 === this.userState.notifyBatteryLevel) {
        this.callNotification();
        this.setBatteryState({ notificationSent : true })
        //clearInterval(this.timerInterval);
      }
      else if(this.userState.batteryState === this.userState.notifyBatteryState 
        && ((this.userState.batteryState && this.userState.batteryLevel*100 >= this.userState.notifyBatteryLevel) 
            || (!this.userState.batteryState && this.userState.batteryLevel*100 <= this.userState.notifyBatteryLevel))) {
        //clearInterval(this.timerInterval);
      }
    }, 1000);

    //setTimeout(() => this.callNotification(), 5000);
    
    // if(this.userState.workerProcess !== undefined && this.workerProcess === null) {
    //   this.workerProcess = this.userState.workerProcess.instance;
    //   Utils.logText('did mount process status:', this.userState.workerProcess.started);
    //   if(!this.userState.workerProcess.started) {
    //     let obj = {'cmd': 'start', 'msg': 'from Home component'};
    //     this.workerProcess.postMessage(obj);
    //     this.setBatteryState({ workerProcess: { instance: this.workerProcess, started: true }});
    //     Utils.logText('background worker process started');
    //   }
    //   this.workerProcess.addEventListener('message', (e) => this.onWorkerMessage(e));
    //   Utils.logText('background worker onmessage attached');
    // }
  }

  componentWillUnmount() {
    clearInterval(this.timerInterval);
    //this.workerProcess.postMessage({'cmd': 'stop', 'msg': 'from main thread'});
    this.setBatteryState({ workerProcess: { instance: this.workerProcess, started: true }});
    Utils.logText('did unmount process status:', this.userState.workerProcess.started);
    this.workerProcess = null;
    Utils.logText('background worker process is still working although home component is unmounted');
  }

  render() {     
    const batteryLevel = this.userState.batteryLevel*100;
    return (
      <Wrapper>
        <div className="App App-body animated heartBeat">
            <div className="circle animated shake delay-1s" 
                style={{ backgroundColor: Utils.bgColor(batteryLevel)
                        , color: Utils.fontColor(batteryLevel) }} >
              <div className="innerCircleContents" >
                <div className="contentHeader" ref="contentHeader">
                  {Utils.getBatteryStateText(this.userState.batteryState)}
                </div>
                <div className="contentBody animated flash" ref="contentBody">
                  {Utils.getBatteryLevelText(this.userState.batteryLevel)}
                </div>
                <div className="contentFooter" ref="contentFooter">
                  {Utils.getBatteryTimeText(this.userState.currBatteryTime)}
                </div>
              </div>
            </div>
        </div>
        <ToastMessage show={this.state.toastMessage === '' ? 'hidden' : 'visible'} 
          classname="animated fadeInUpBig delay-2s" message={this.state.toastMessage}></ToastMessage>
      </Wrapper>
    );
  }
}

const Wrapper = styled.div`
.App {
  text-align: center;  
  background-color: #282c34;
  min-height: 100vh;
}

.App-body {
  display: grid;
  place-items:center;
}

.circle {
  width:45vmax;
  height:45vmax;
  border-radius:50%;
  box-sizing: border-box;
  position:relative;
  background:rgb(240, 16, 102);
  color: black;
  font-family: Helvetica, Arial Black, sans;
  border:solid 0px greenyellow;
  transform: scale(1);
  transition:transform ease-in-out 250;
}

.circle:hover {
  transform:scale(1.010);
}

.innerCircleContents {
  margin:auto;
  padding:25% 0;
  width:100%;
  height:50%;
  place-items:center;
  display: grid;
  grid-auto-flow:row;
  grid-gap: 5px;
}

.contentHeader {
  width:50%;
  border:0px solid greenyellow;
  font-size: calc(8px + 4vmin);
}

.contentBody {
  width:100%;
  border:0px solid greenyellow;
  font-size: calc(8px + 20vmin);
}

.contentFooter {
  border:0px solid greenyellow;
  width:50%;
  font-size: calc(8px + 2vmin);
  word-wrap: break-word;
  overflow-wrap: break-word;
}

`
export default Home;
