import React from "react";
import styled from "styled-components";

function ToastMessage(props) {
  return (
    <Wrapper className={props.classname} style={{ visibility: props.show }} >
      {[...props.message].map((text, index) => <div key={index} >{text}</div>)}
    </Wrapper>
  );
}

const Wrapper = styled.div`
 
  visibility: hidden; /* Hidden by default. Visible on click */
  // min-width: 280px; /* Set a default minimum width */
  // margin-left: -140px; /* Divide value of min-width by 2 */
  width:50vw;
  background-color: rgba(255, 255, 153, .8); /* background color */
  color: black; /* text color */
  text-align: center; /* Centered text */
  border-radius: 5px; /* Rounded borders */
  padding: 12px; /* Padding */
  position: fixed; /* Sit on top of the screen */
  z-index: 1; /* Add a z-index if needed */
  left: 25%; /* Center the snackbar */
  bottom: 30px; /* 30px from the bottom */
  font-size:.8em;

`;

export default ToastMessage
